import React from 'react';
import s from './Header.module.css';
import { NavLink } from 'react-router-dom';
import userPhoto from '../../assets/images/user.jpg';

const Header = (props) => {
    return (
        <header className={s.header}>
            <img className={s.logo} src='https://play-lh.googleusercontent.com/ahJtMe0vfOlAu1XJVQ6rcaGrQBgtrEZQefHy7SXB7jpijKhu1Kkox90XDuH8RmcBOXNn'/>
            <div className={s.loginBlock}>
                {props.isAuth 
                ? <div className={s.trueLoginBlock}>
                    <b className={s.userLogin}>{props.login}</b>
                    <img className={s.userPhoto} src={props.photo || userPhoto} />
                    <button className={s.logout} onClick={props.logout} >Log out</button>
                </div>
                : <NavLink className={s.defaultLogin} to={'/login'}>Login</NavLink>} 
            </div>
        </header>
    );
}

export default Header;