const LIKE = 'LIKE';
const DISLIKE = 'DISLIKE';
const SET_TRACKS = 'SET-TRACKS';

let initialState = {
    tracks: []
}

const musicReducer = (state=initialState, action) => {
    switch (action.type) {
        case LIKE:
            return {
                ...state,
                tracks: state.tracks.map( t => {
                    if (t.id === action.trackId) {
                        return {...t, liked: true};
                    } else {
                        return t;
                    }
                })
            }
        case DISLIKE: 
            return {
                ...state,
                tracks: state.tracks.map( t => {
                    if (t.id === action.trackId) {
                        return {...t, liked: false};
                    } else {
                        return t;
                    }
                })
            }
        case SET_TRACKS:
            return {
                ...state,
                tracks: [...state.tracks, ...action.tracks]
            }
        default:
            return {
                ...state
            }
    }
}

export const likeAC = (trackId) => ({
    type: LIKE,
    trackId
})

export const dislikeAC = (trackId) => ({
    type: DISLIKE,
    trackId
})

export const setTracksAC = (tracks) => ({
    type: SET_TRACKS,
    tracks
})

export default musicReducer;