import React from 'react';
import s from './ProfileInfo.module.css';
import Preloader from '../../common/Preloader/Preloader';
import yes from '../../../assets/images/yes.png';
import no from '../../../assets/images/no.png';
import userPhoto from '../../../assets/images/user.jpg'
import ProfileStatusWithHooks from './ProfileStatus/ProfileStatusWithHooks';

const ProfileInfo = (props) => {
    if (!props.profile) {
        return <Preloader />
    }
    return (
        <div>
            <div>
                <img width='100%' height='150px' src='https://helpx.adobe.com/content/dam/help/en/photoshop/using/convert-color-image-black-white/jcr_content/main-pars/before_and_after/image-before/Landscape-Color.jpg' />
            </div>
            <div className={s.profileBlock}>
                <img className={s.userPhoto} src={props.profile.photos.large || userPhoto} />
                <div className={s.titleBlock}>
                    <h1 className={s.userName}>{props.profile.fullName}</h1>
                    <ProfileStatusWithHooks status={props.status} updateUserStatus={props.updateUserStatus} />
                </div>
                <div className={s.infoBlock}>
                    <h2>:</h2>
                    <ul>
                        {(props.profile.aboutMe) ? 
                            <li>
                            <span className={s.listTheme}> : </span>
                            <span className={s.listDescription}>{props.profile.aboutMe}</span>
                        </li> : null
                        }
                        <li>
                            <span className={s.listTheme}>: </span>
                            <span className={s.listDescription}><ul>
                                {(props.profile.contacts.facebook) ? 
                                    <li><a href={props.profile.contacts.facebook}>facebook</a></li> : null
                                }
                                {(props.profile.contacts.website) ? 
                                    <li><a href={props.profile.contacts.website}>-</a></li> : null
                                }
                                {(props.profile.contacts.vk) ? 
                                    <li><a href={props.profile.contacts.vk}>vk</a></li> : null
                                }
                                {(props.profile.contacts.twitter) ? 
                                    <li><a href={props.profile.contacts.twitter}>twitter</a></li> : null
                                }
                                {(props.profile.contacts.instagram) ? 
                                    <li><a href={props.profile.contacts.instagram}>instagram</a></li> : null
                                }
                                {(props.profile.contacts.youtube) ? 
                                    <li><a href={props.profile.contacts.youtube}>youtube</a></li> : null
                                }
                                {(props.profile.contacts.github) ? 
                                    <li><a href={props.profile.contacts.github}>github</a></li> : null
                                }
                                {(props.profile.contacts.mainLink) ? 
                                    <li><a href={props.profile.contacts.mainLink}>mainLink</a></li> : null
                                }
                            </ul></span>
                        </li>
                        <li>
                            <span className={s.listTheme}> : </span>
                            <span className={s.listDescription}><img className={s.jobPhoto} src={props.profile.lookingForAJob ? yes : no} /></span>
                        </li>
                        {(props.profile.lookingForAJob) ? 
                            <li>
                            <span className={s.listTheme}> : </span>
                            <span className={s.listDescription}>{props.profile.lookingForAJobDescription || ''}</span>
                        </li> : null
                        }
                    </ul>
                </div>
            </div>
        </div>
    );
}

export default ProfileInfo;