import { stopSubmit } from "redux-form";
import { authAPI } from "../api/api";

const SET_USER_DATA = 'auth/SET-USER-DATA';
const LOGIN_USER = 'LOGIN-USER';

let initialState = {
    userId: null,
    login: null,
    email: null,
    isAuth: false,
    isFetching: false
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
            }
        case LOGIN_USER:
            return {
                ...state,
                isAuth: true,
                email: action.email,
                login: action.login,
            }
        default:
            return {
                ...state
            }
    }
}

export const setAuthUserData = (userId, login, email, isAuth) => ({
    type: SET_USER_DATA,
    payload: {
        userId, login, email, isAuth
    }
})

