import React from 'react';
import s from './../Dialogs.module.css'


const Message = (props) => {

    let sideMessage = props.id % 2 === 0 ? s.rightMessage : s.leftMessage;

    return ( 
        <div className={s.message + ' ' + sideMessage}>
            {props.message}
        </div>
    );
}

export default Message;